import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-block2',
  templateUrl: './block2.component.html',
  styleUrls: ['./block2.component.css']
})
export class Block2Component implements OnInit, OnDestroy {
  @Input() ImageUrl : string | ArrayBuffer;
  @Input() IDD : number;
  @Input() Value : number;
  @Output() childEvent = new EventEmitter<Number>();
  ClassNameValue : string;
  ClassNameTile : string;
    _maxRow : number = 4;
    _maxCol : number = 4;
    _width : number = 200;
    _height : number = 200;
  postTop : number;
  postLeft : number;
  timer : number;

  ngOnInit(): void {
    this.ClassNameValue = `img img-${this.Value}`;
    this.ClassNameTile = `tile tile-${this.IDD}`;
    let ix = Math.floor(this.IDD / 4);
    let iy = this.IDD % 4;
    this.postTop = ix * this._width;
    this.postLeft = iy * this._height;
  }

  ngOnDestroy(): void {
      if (this.timer)
        clearInterval(this.timer)
  }

  get Top() {
    return `${this.postTop}px`;
  }
  get Left() {
    return `${this.postLeft}px`;
  }
  onChildClick() {
      console.log(this.Value);
    //this.childEvent.emit(this.IDD);

    //check valid click
    this.timer = setInterval(() => {
        this.moveTileUp()
        if (this.postTop <= 0)
            clearInterval(this.timer);
    }, 16)
    
  }

  moveTileUp() {
      this.postTop -= 10;
  }

  moveTileDown() {
      this.postTop += 10;
  }

}
