import { Component, ANALYZE_FOR_ENTRY_COMPONENTS, HostListener } from '@angular/core';
import { ITile, Tile, ITileList, Direction } from './models/ITile';
import { AppSettings } from './AppSettings';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'Sliding Puzzle';
  Board : Tile[];
  private _tileSize : number = 200;
  imageUrl : string | ArrayBuffer;
  timer : number = -1;

  ngOnInit(): void {
    //this.Board = this.initTiles();
    console.log(`${window.innerWidth} x ${window.innerHeight}`);
    let size = Math.min(window.innerWidth,window.innerHeight);
    size = Math.floor((size - 20) / 4);
    this._tileSize = Math.min(this._tileSize, size)
    this.Board = this.initTiles(this.initEmptyValues())
  }
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    console.log(`${event.target.innerWidth} x ${event.target.innerHeight}`);
    let size = Math.min(event.target.innerWidth, event.target.innerHeight);
    //this._tileSize = Math.floor(size / AppSettings.SideCnt);
    ////console.log(this._tileSize);
  }


  get Size() : string {
    return (this._tileSize * AppSettings.SideCnt)+ 'px';
  }

  initTiles(result : number[]) : Tile[]{
    let tiles : Tile[] = [];
    for(var i=0; i<result.length; i++)
      tiles.push(
        new Tile(i, result[i])
          .initImage(AppSettings.SideCnt, this._tileSize)
          .resetPost(AppSettings.SideCnt, this._tileSize)
      );
    return tiles;
  }

  initEmptyValues() : number[] {
    let result : number[] = new Array(16);
    for (var i =0; i < result.length; i++) {
      result[i] = 0;
    }
    return result;    
  }

  initTestValues() : number[] {
    let result : number[] = new Array(15);
    for (var i =0; i < result.length; i++) {
      result[i] = i+1;
    }
    return result;
  }
  initValues():number[] { 
    let result : number[] = new Array(15);
    for (var i =0; i < result.length; i++) {
      let isValid = false;
      while (!isValid) {
        result[i] = Math.floor(Math.random() * 15) + 1;
        // validation
        isValid = true;
        for (var j = 0; j < i; j++) {
         if (result[j] == result[i]) 
          isValid = false;
        }
      }
    }
    //console.log(result);
    return result;
  } 

  onListenImage($event : string | ArrayBuffer) {    
    this.imageUrl = $event;
    //this.Board = this.initTiles(this.initValues());
    this.Board = this.initTiles(this.initTestValues());
  }

  onListenChild($event : any) {
    if (this.timer == -1) {
      let id = $event as number;
      console.log($event);
      this.checkClick(id);
    }
  }

  getIdx(ix : number, iy : number) {
    return ix * AppSettings.SideCnt + iy;
  }

  moveTile(tile: Tile, toId : number, dir : Direction) {
    let limit = 0;
    let stepSize = this._tileSize / 10;
    if (dir == Direction.Up)
      limit = tile.top - this._tileSize;
    if (dir == Direction.Down)
      limit = tile.top + this._tileSize;
    if (dir == Direction.Left)
      limit = tile.left - this._tileSize;
    if (dir == Direction.Right)
      limit = tile.left + this._tileSize;
    this.timer = setInterval(() => {
      let isFinish = false;
      if (dir == Direction.Up) {
        tile.top -= Math.min(Math.abs(tile.top - limit), stepSize);
        isFinish = tile.top == limit;
      }
      if (dir == Direction.Down) {
        tile.top += Math.min(Math.abs(tile.top - limit), stepSize);
        isFinish = tile.top == limit;
      }
      if (dir == Direction.Left) {
        tile.left -= Math.min(Math.abs(tile.left - limit), stepSize);
        isFinish = tile.left == limit;
      }
      if (dir == Direction.Right) {
        tile.left += Math.min(Math.abs(tile.left - limit), stepSize);
        isFinish = tile.left == limit;
      }
      if (isFinish) {
          clearInterval(this.timer);
          tile.id = toId;
          tile.resetPost(AppSettings.SideCnt, this._tileSize);
          this.timer = -1;

          if (this.isCompleted())
            alert("Puzzle is completed");
      }
    }, 16, tile, dir, stepSize, limit)

    // setinteval to move tile
    // set tile.id = toID while finish
    // enable click event;
  }

  isCompleted() : boolean{
    for(let i=0; i<this.Board.length; i++){
      if (!this.Board[i].isValid())
        return false;
    }
    return true;
  }

  

  getTile(idx : number) : Tile {
    for(let i=0; i<this.Board.length; i++) {
      if (this.Board[i].id == idx)
        return this.Board[i];
    }
    return null;
  }
  

  checkClick(idx : number) : void {
    let ix = Math.floor(idx / AppSettings.SideCnt);
    let iy = idx % AppSettings.SideCnt;
    console.log(`${ix}, ${iy}`)
    let currentTile = this.getTile(idx);
    let targetIdx = -1;
    let actionTaken = false;

    if (!actionTaken && (ix-1 >= 0)) {
      //up
      targetIdx = this.getIdx(ix-1, iy);
      if (!this.getTile(targetIdx)) {
        actionTaken = true;
        this.moveTile(currentTile, targetIdx, Direction.Up);
      }
    }
    if (!actionTaken && (ix+1 < AppSettings.SideCnt)) {
      //down
      targetIdx = this.getIdx(ix+1, iy);
      if (!this.getTile(targetIdx)) {
        actionTaken = true;
        this.moveTile(currentTile, targetIdx, Direction.Down);
      }
    }
    if (!actionTaken && (iy-1 >= 0)) {
      //left
      targetIdx = this.getIdx(ix, iy-1);
      if (!this.getTile(targetIdx)) {
        actionTaken = true;
        this.moveTile(currentTile, targetIdx, Direction.Left);
      }
    }
    if (!actionTaken && (iy+1 < AppSettings.SideCnt)) {
      //right
      targetIdx = this.getIdx(ix, iy+1);
      if (!this.getTile(targetIdx)) {
        actionTaken = true;
        this.moveTile(currentTile, targetIdx, Direction.Right);
      }
    }
  }
}
