import { NumberFormatStyle } from '@angular/common';

export interface ITile{
    id: number;
    value:number;
    top: number;
    left: number;
    size: number;
    imageTop: number;
    imageLeft: number;
    isValid:()=> boolean;
    //initImage:(sideCnt : number, baseSize : number) => ITile;
    initImage(sideCnt : number, baseSize : number) : ITile;
    resetPost:(sideCnt : number, baseSize : number) => ITile;
}

export interface ITileList {
    [index: number] : ITile
}

export class Tile implements ITile {

    public top:number;
    public left:number;
    public size:number;
    public imageTop: number;
    public imageLeft: number;

    constructor(public id : number, public value : number) {
        //this.id = id;
        //this.value = value;
    }

    initImage(sideCnt : number, imageSize : number) : ITile {
        this.imageTop = Math.floor((this.value - 1) / sideCnt);
        this.imageLeft = (this.value - 1) % sideCnt;
        this.imageTop = this.imageTop * imageSize * -1;
        this.imageLeft = this.imageLeft * imageSize * -1;
        return this;
    }

    isValid() : boolean {
        return this.id + 1 == this.value;
    }

    resetPost(sideCnt : number, size : number) : ITile {
        this.size = size;
        this.top = Math.floor(this.id / sideCnt);
        this.left = this.id % sideCnt;
        this.top = this.top * size;
        this.left = this.left * size;
        return this;
    }
}

export class TileTest {
    /**
     *
     */
    constructor(public id : number, public value : number) {
    }

    getSum() : number {
        return this.id + this.value;
    }
}

export const enum Direction {
    Up = 1,
    Down,
    Left,
    Right
}
